@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?14dnvj');
  src:  url('../fonts/icomoon.eot?14dnvj#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?14dnvj') format('truetype'),
    url('../fonts/icomoon.woff?14dnvj') format('woff'),
    url('../fonts/icomoon.svg?14dnvj#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "\f09a";
}
.icon-facebook-f:before {
  content: "\f09a";
}
.icon-github:before {
  content: "\f09b";
}
.icon-envelope:before {
  content: "\f0e0";
}
.icon-linkedin:before {
  content: "\f0e1";
}
.icon-instagram:before {
  content: "\f16d";
}
.icon-skype:before {
  content: "\f17e";
}
